
Template.__checkName("categories_menu_item");
Template["categories_menu_item"] = new Template("Template.categories_menu_item", (function() {
  var view = this;
  return [ Blaze.If(function() {
    return Spacebars.call(view.lookup("showMultiple"));
  }, function() {
    return [ "\n\n    ", HTML.SPAN({
      "class": "menu-item-label"
    }, "\n      ", HTML.SPAN({
      "class": "menu-item-label-text js-category-toggle"
    }, "\n        ", HTML.LABEL({
      "class": "category-label"
    }, "\n          ", HTML.INPUT(HTML.Attrs({
      type: "checkbox",
      "class": "category-checkbox",
      name: function() {
        return Spacebars.mustache(Spacebars.dot(view.lookup("item"), "data", "_id"));
      }
    }, function() {
      return Spacebars.attrMustache(view.lookup("isChecked"));
    })), " \n          ", Blaze.View("lookup:getItemLabel", function() {
      return Spacebars.makeRaw(Spacebars.mustache(view.lookup("getItemLabel")));
    }), "\n        "), "\n      "), "\n      ", Blaze.If(function() {
      return Spacebars.call(view.lookup("description"));
    }, function() {
      return HTML.SPAN({
        "class": "menu-item-label-description"
      }, Blaze.View("lookup:getDescription", function() {
        return Spacebars.makeRaw(Spacebars.mustache(view.lookup("getDescription")));
      }));
    }), "\n    "), "\n\n  " ];
  }, function() {
    return [ "\n\n    ", HTML.A({
      "class": "menu-item-label",
      href: function() {
        return Spacebars.mustache(view.lookup("itemRoute"));
      }
    }, "\n      ", HTML.SPAN({
      "class": "menu-item-label-text"
    }, Blaze.View("lookup:getItemLabel", function() {
      return Spacebars.makeRaw(Spacebars.mustache(view.lookup("getItemLabel")));
    })), "\n      ", Blaze.If(function() {
      return Spacebars.call(view.lookup("description"));
    }, function() {
      return HTML.SPAN({
        "class": "menu-item-label-description"
      }, Blaze.View("lookup:getDescription", function() {
        return Spacebars.makeRaw(Spacebars.mustache(view.lookup("getDescription")));
      }));
    }), "\n    "), "\n\n  " ];
  }), "\n\n  ", Blaze.If(function() {
    return Spacebars.call(view.lookup("childMenuItems"));
  }, function() {
    return [ "\n    ", HTML.A({
      "class": "menu-items-toggle js-menu-toggle",
      href: "#"
    }, "\n      ", Spacebars.include(view.lookupTemplate("menuIconExpand")), "\n      ", Spacebars.include(view.lookupTemplate("menuIconCollapse")), "\n    "), "\n  " ];
  }) ];
}));
