
Template.__checkName("category_item");
Template["category_item"] = new Template("Template.category_item", (function() {
  var view = this;
  return HTML.DIV({
    "class": "form-module"
  }, "\n    ", Blaze._TemplateWith(function() {
    return {
      collection: Spacebars.call("Categories"),
      id: Spacebars.call(view.lookup("formId")),
      type: Spacebars.call("update"),
      doc: Spacebars.call(view.lookup("category")),
      "label-class": Spacebars.call("control-label"),
      "input-col-class": Spacebars.call("controls"),
      template: Spacebars.call("bootstrap3-horizontal")
    };
  }, function() {
    return Spacebars.include(view.lookupTemplate("quickForm"));
  }), HTML.Raw('\n    <a href="#" class="delete-link">Delete</a>\n  '));
}));
