
Template.__checkName("post_categories");
Template["post_categories"] = new Template("Template.post_categories", (function() {
  var view = this;
  return HTML.DIV({
    "class": function() {
      return [ "post-categories ", Spacebars.mustache(view.lookup("moduleClass")) ];
    }
  }, "\n    ", Blaze.Each(function() {
    return Spacebars.call(view.lookup("categoriesArray"));
  }, function() {
    return [ "\n      ", HTML.A({
      href: function() {
        return Spacebars.mustache(view.lookup("categoryLink"));
      },
      "class": function() {
        return [ "post-category category-", Spacebars.mustache(view.lookup("slug")) ];
      }
    }, Blaze.View("lookup:name", function() {
      return Spacebars.mustache(view.lookup("name"));
    })), "\n    " ];
  }), "\n  ");
}));
