
Template.__checkName("afCategory_bootstrap3");
Template["afCategory_bootstrap3"] = new Template("Template.afCategory_bootstrap3", (function() {
  var view = this;
  return HTML.DIV(HTML.Attrs({
    "class": "category-input"
  }, function() {
    return Spacebars.attrMustache(Spacebars.dot(view.lookup("."), "atts"));
  }), "\n    ", Blaze._TemplateWith(function() {
    return {
      menuName: Spacebars.call("categories"),
      menuItems: Spacebars.call(view.lookup("menuItems")),
      menuType: Spacebars.call("collapsible")
    };
  }, function() {
    return Spacebars.include(view.lookupTemplate("menuComponent"));
  }), "\n  ");
}));

Template.__checkName("category_input_item");
Template["category_input_item"] = new Template("Template.category_input_item", (function() {
  var view = this;
  return [ HTML.DIV({
    "class": "menu-item-label category-input-item",
    id: function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("item"), "_id"));
    }
  }, "\n    ", HTML.LABEL("\n      ", HTML.INPUT(HTML.Attrs({
    "class": "category-checkbox",
    type: "checkbox",
    value: function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("item"), "_id"));
    }
  }, function() {
    return Spacebars.attrMustache(view.lookup("atts"));
  })), " ", Blaze.View("lookup:getItemLabel", function() {
    return Spacebars.makeRaw(Spacebars.mustache(view.lookup("getItemLabel")));
  }), "\n    "), "\n  "), "\n  ", Blaze.If(function() {
    return Spacebars.call(view.lookup("childMenuItems"));
  }, function() {
    return [ "\n    ", HTML.A({
      "class": "menu-items-toggle js-menu-toggle",
      href: "#"
    }, "\n      ", HTML.SPAN({
      "class": "menu-icon-expand"
    }, Spacebars.include(view.lookupTemplate("menuIconExpand"))), "\n      ", HTML.SPAN({
      "class": "menu-icon-collapse"
    }, Spacebars.include(view.lookupTemplate("menuIconCollapse"))), "\n    "), "\n  " ];
  }) ];
}));
