
Template.__checkName("categories_admin");
Template["categories_admin"] = new Template("Template.categories_admin", (function() {
  var view = this;
  return [ HTML.DIV({
    "class": "form-well add-category"
  }, "\n    ", HTML.Raw("<h3>Add new category:</h3>"), "\n    ", Blaze._TemplateWith(function() {
    return {
      collection: Spacebars.call("Categories"),
      id: Spacebars.call("insertCategoryForm"),
      type: Spacebars.call("insert"),
      "label-class": Spacebars.call("control-label"),
      "input-col-class": Spacebars.call("controls"),
      template: Spacebars.call("bootstrap3-horizontal")
    };
  }, function() {
    return Spacebars.include(view.lookupTemplate("quickForm"));
  }), "\n  "), "\n  ", HTML.DIV({
    "class": "categories-list"
  }, "\n    ", Blaze._TemplateWith(function() {
    return {
      menuName: Spacebars.call("categoriesAdmin"),
      menuItems: Spacebars.call(view.lookup("menuItems")),
      menuType: Spacebars.call("list")
    };
  }, function() {
    return Spacebars.include(view.lookupTemplate("menuComponent"));
  }), "\n  ") ];
}));
