
Template.__checkName("categories_menu");
Template["categories_menu"] = new Template("Template.categories_menu", (function() {
  var view = this;
  return HTML.DIV({
    "class": function() {
      return [ "categories-menu-wrapper ", Spacebars.mustache(view.lookup("moduleClass")) ];
    }
  }, "\n    ", Blaze.If(function() {
    return Spacebars.call(view.lookup("hasCategories"));
  }, function() {
    return [ "\n      ", Blaze._TemplateWith(function() {
      return {
        menuName: Spacebars.call("categories"),
        menuLabel: Spacebars.call(view.lookup("menuLabel")),
        itemTemplate: Spacebars.call("categories_menu_item"),
        menuItems: Spacebars.call(view.lookup("menuItems")),
        menuType: Spacebars.call(view.lookup("menuType")),
        expandLevel: Spacebars.call(view.lookup("expandLevel"))
      };
    }, function() {
      return Spacebars.include(view.lookupTemplate("menuComponent"));
    }), "\n    " ];
  }), "\n  ");
}));
