
Template.__checkName("category_title");
Template["category_title"] = new Template("Template.category_title", (function() {
  var view = this;
  return Blaze.If(function() {
    return Spacebars.call(view.lookup("categories"));
  }, function() {
    return [ "\n    ", HTML.DIV({
      "class": "category-heading-wrapper post-list-title"
    }, "\n      ", Blaze.Each(function() {
      return Spacebars.call(view.lookup("categories"));
    }, function() {
      return [ "\n        ", HTML.DIV({
        "class": "category-heading"
      }, "\n          ", HTML.DIV({
        "class": "category-title"
      }, "\n            ", Spacebars.With(function() {
        return Spacebars.call(view.lookup("categoryParents"));
      }, function() {
        return [ "\n              ", HTML.DIV({
          "class": "category-parents"
        }, "\n                ", Blaze.Each(function() {
          return Spacebars.call(view.lookup("."));
        }, function() {
          return [ "\n                  ", HTML.A({
            href: function() {
              return Spacebars.mustache(Spacebars.dot(view.lookup("."), "getUrl"));
            }
          }, Blaze.View("lookup:title", function() {
            return Spacebars.mustache(view.lookup("title"));
          })), " ", Blaze.View("lookup:icon", function() {
            return Spacebars.makeRaw(Spacebars.mustache(view.lookup("icon"), "next"));
          }), "\n                " ];
        }), "\n              "), "\n            " ];
      }), "\n            ", HTML.H2({
        "class": "main-category-title"
      }, Blaze.View("lookup:title", function() {
        return Spacebars.mustache(view.lookup("title"));
      })), "\n          "), "\n          ", Blaze.If(function() {
        return Spacebars.call(view.lookup("description"));
      }, function() {
        return [ "\n            ", HTML.H3({
          "class": "category-description"
        }, Blaze.View("lookup:description", function() {
          return Spacebars.mustache(view.lookup("description"));
        })), "\n          " ];
      }), "\n        "), "\n      " ];
    }), "\n    "), "\n  " ];
  });
}));
